import CryptoJS from 'crypto-js';

// IV
// 
const KEY = CryptoJS.enc.Utf8.parse('1234567890123456'); // 16
const IV = CryptoJS.enc.Utf8.parse('1234567890123456'); // 16IV

/**
 * AES 
 * @param {string} text - 
 * @returns {string} - Base64
 */
export function encrypt(text) {
  const encrypted = CryptoJS.AES.encrypt(CryptoJS.enc.Utf8.parse(text), KEY, {
    iv: IV,
    mode: CryptoJS.mode.CBC,
    padding: CryptoJS.pad.Pkcs7,
  });
  return encrypted.toString(); // Base64
}

/**
 * AES 
 * @param {string} encryptedText - Base64
 * @returns {string} - 
 */
export function decrypt(encryptedText) {
  const decrypted = CryptoJS.AES.decrypt(encryptedText, KEY, {
    iv: IV,
    mode: CryptoJS.mode.CBC,
    padding: CryptoJS.pad.Pkcs7,
  });
  return decrypted.toString(CryptoJS.enc.Utf8);
} 