/* eslint-disable no-unused-vars */
import axios from 'axios';
import dialog from '@/utils/dialog' ;
import router from '@/router/index';
import {ElMessage} from "element-plus"; // 
import {useUserStore} from '@/store/userStore';



let isShowLoginTip = false;

const axiosExt = axios.create({
    timeout: 10000, // 
    withCredentials: true //  Cookie
});

/// 
axiosExt.interceptors.request.use(
    config => {
        //  token  headers 
        // cookie - session  Token
        //  token
        // const token = localStorage.getItem('userToken');
        // if (token) {
        //     config.headers['Authorization'] = `Bearer ${token}`;
        // }
        return config;
    },
    error => {
        // 
        return Promise.reject(error);
    }
);


/**
 *  
 * 1 
 * 2401 403 500
 * 3 
 *
 */


/////////////////////////////
let errorResponse = {
    success: false,
    message: '',
    data: null,
    status: 500
}

// 
function showError(msg) {
    ElMessage({
        message: msg || '',
        type: 'error',
        showClose: true,
        duration: 3000
    });
}

/// 
axiosExt.interceptors.response.use(
    response => {
        // 
        console.log("response",response)
        console.log("response-data",response.data)
        const res = response.data;


        // 1.  code/msg
        if (res && typeof res.code !== 'undefined' && res.code !== 0) {
            showError(res.msg || '');
            return Promise.reject(res);
        }

        // 2.  status 1005 
        if (res && res.status === 1005) {
            showError(res.message || '');
            return Promise.reject(res);
        }
        return res;
    },
    error => {
        const {response} = error;
        const userStore = useUserStore();
        if (response) {
            switch (response.status) {
                case 401: {
                    userStore.logout();
                    if (isShowLoginTip){
                        return Promise.reject(response.data) ;
                    }
                    isShowLoginTip = true ;
                    dialog.alter("", "", () => {
                        router.push({path: '/login'})
                            .then(() => {
                                isShowLoginTip = false ;
                            });
                    }, () => {
                        router.push({path: '/login'})
                            .then(() => {
                                isShowLoginTip = false ;
                            });
                    })
                    return Promise.reject(response.data) ;
                }
                case 403: {
                    showError('');
                    router.push('/index');
                    return Promise.reject(response.data)
                }

                case 500: {
                    showError((response.data && response.data.message) || '');
                    return Promise.reject(response.data || { message: '' });
                }

                default: {
                    showError((response.data && response.data.message) || `(${response.status})`);
                    return Promise.reject(response.data);
                }
            }
        } else {
            // 
            showError(error.message || '');
            return Promise.reject(error);
        }
    }
);

export default axiosExt;