<template>
  <div>
    <div class="setting-section">
      <div class="setting-title"></div>
      <el-form :model="nicknameForm" ref="nicknameFormRef" :rules="nicknameRules" label-width="80px">
        <el-form-item label="" prop="nickname">
          <el-input v-model="nicknameForm.nickname" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitNicknameForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="setting-section">
      <div class="setting-title"></div>
      <el-form :model="passwordForm" ref="passwordFormRef" :rules="passwordRules" label-width="80px">
        <el-form-item label="" prop="oldPassword">
          <el-input type="password" v-model="passwordForm.oldPassword" show-password></el-input>
        </el-form-item>
        <el-form-item label="" prop="newPassword">
          <el-input type="password" v-model="passwordForm.newPassword" show-password></el-input>
        </el-form-item>
        <el-form-item label="" prop="confirmPassword">
          <el-input type="password" v-model="passwordForm.confirmPassword" show-password></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitPasswordForm"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script setup>
import { ref, reactive } from 'vue';
import { useRouter } from 'vue-router';
import { useUserStore } from '@/store/userStore';
import { updateUser } from '@/api/user';
import { ElMessage, ElNotification } from 'element-plus';

const router = useRouter();
const userStore = useUserStore();

// ---  ---
const nicknameFormRef = ref(null);
const nicknameForm = reactive({
  nickname: ''
});
const nicknameRules = {
  nickname: [
    { required: true, message: '', trigger: 'blur' },
    { min: 2, max: 20, message: ' 2  20 ', trigger: 'blur' }
  ]
};

const submitNicknameForm = async () => {
  if (!nicknameFormRef.value) return;
  await nicknameFormRef.value.validate(async (valid) => {
    if (valid) {
      try {
        await updateUser({ nickname: nicknameForm.nickname });
        userStore.user.nickname = nicknameForm.nickname;
        const userInfo = JSON.parse(localStorage.getItem('userInfo'));
        if (userInfo) {
          userInfo.nickname = nicknameForm.nickname;
          localStorage.setItem('userInfo', JSON.stringify(userInfo));
        }
        ElMessage.success('');
        nicknameForm.nickname = '';
      } catch (error) {
        ElNotification.error({
          title: '',
          message: error.message || ''
        });
      }
    }
  });
};

// ---  ---
const passwordFormRef = ref(null);
const passwordForm = reactive({
  oldPassword: '',
  newPassword: '',
  confirmPassword: ''
});

const validateConfirmPassword = (rule, value, callback) => {
  if (value === '') {
    callback(new Error(''));
  } else if (value !== passwordForm.newPassword) {
    callback(new Error(''));
  } else {
    callback();
  }
};

const passwordRules = {
  oldPassword: [{ required: true, message: '', trigger: 'blur' }],
  newPassword: [
    { required: true, message: '', trigger: 'blur' },
    { min: 6, message: '6', trigger: 'blur' }
  ],
  confirmPassword: [
    { required: true, validator: validateConfirmPassword, trigger: 'blur' }
  ]
};

const submitPasswordForm = async () => {
  if (!passwordFormRef.value) return;
  await passwordFormRef.value.validate(async (valid) => {
    if (valid) {
      try {
        await updateUser({
          oldPassword: passwordForm.oldPassword,
          newPassword: passwordForm.newPassword
        });
        ElMessage.success('');
        userStore.logout();
        localStorage.removeItem('userInfo');
        await router.push('/login');
      } catch (error) {
        ElNotification.error({
          title: '',
          message: error.message || ''
        });
      }
    }
  });
};
</script>

<style scoped>
.setting-section {
  max-width: 500px;
}
.setting-section:not(:first-child) {
    margin-top: 20px;
}
.setting-title {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 20px;
}
</style> 